@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  font-family:  'Lato',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(252, 252, 252);
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



@media screen and (min-width: 25em){
  html { font-size: calc( 16px + (24 - 16) * (100vw - 400px) / (800 - 400) ); }
}

body, html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

p {
  font-size: 16px;
  line-height: 150%;
}

a {
  font-size: 16px;
}

li {
  font-size: 16px;
}

h2, h3, h4 {
  color: rgb(80, 85, 87);
  padding: 8px;
}

.App {
  max-width: 100%;
}

.flex {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
}

.flex > * {
  flex: 1 1 250px;
  margin: 10px;
}

.section {
  padding-top: 62px;
  max-width: 100%;
}

.section h1 {
  color: rgb(29, 30, 31);
  font-size: min(2em, 70px);
}

.bottom-spacer {
  height: 70vh;
}

/* Navbar */

.navbar {
  position: fixed;
  width: 100%;
  background-color: rgb(252, 252, 252);
  z-index: 1;
  display: flex;
  top: -100px;
  left: 0;
  transition: top 0.5s;
  justify-content: space-between;
  max-width: 100%;
  padding: 16px;
}

.nav-left {
  display: flex;
}

.arrow {
  border: solid rgb(252, 252, 252);
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 4px;
}

.active {
  top: 0;
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.nav-item {
  color: rgba(55, 64, 66, 0.568);
  font-weight: bold;
  border: none;
  cursor: pointer;
  outline: none;  
  background-color: transparent;
  transition: all 0.5s;
  margin-right: 1em;
  justify-content: center;
  font-size: 16px;
}

.navbar .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  nav .nav-items button {display: none}
  nav .nav-contact-methods{display: none;}
  .navbar a.icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 22px;
    padding: 16px;
  }
  nav { 
    
  box-shadow: 0 8px 16px 0 rgba(55, 64, 66,0.2), 0 6px 20px 0 rgba(55, 64, 66,0.19);
  }
}

@media screen and (max-width: 600px) {
  .minimize {
    flex-wrap: wrap;
    flex-direction: column;
  }
  .minimize .nav-items button {
    float: none;
    display: flex;
    text-align: center;
    margin: auto;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 8px;
  }
}

.selected {
  color: #11bb9a;
}

.nav-logo a {
  text-decoration: none;
}

.nav-contact-methods {
  margin-left: 0.25em;
}

.nav-contact-methods a {
  margin-left: 1em;
}

.nav-logo {
  font-size: 28px;
  border: none;
  cursor: pointer;
  outline: none;  
  background-color: transparent;
  font-weight: bold;
}

/*Introduction*/

.container-intro {
  height: 98vh;
  background-color: rgb(252, 252, 252);
  transition: all 0.5s;
  margin-top: 5em;
  max-width: 100%;
}

.trans {
  background-color: rgb(252, 252, 252);
  background-image: none;
}

.name {
  line-height: .8em;
  -webkit-animation: loadName 0.5s;
          animation: loadName 0.5s;
}

.role {
  font-size: min(1em, 35px);
  -webkit-animation: loadName .6s;
          animation: loadName .6s;
}

.cta-button {
  background-color: #11bb99;
  color: rgb(252, 252, 252); 
  font-size: 16px;
  width: 8em;
  max-width: 200px;
  margin-top: 10px;
  padding: 10px;
  box-shadow: 0 8px 16px 0 rgba(55, 64, 66,0.2), 0 6px 20px 0 rgba(55, 64, 66,0.19);
  transition: loadName 0.5s;
  cursor: pointer;
  font-weight: bold;
  border: none;
  outline: none;
  opacity: 0;
  -webkit-animation: loadName 1s 1s;
          animation: loadName 1s 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.cta-button:hover {
  background-color: #11bb99ad;
}

@-webkit-keyframes loadName{
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes loadName{
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

.container-projects{
    position: relative;
    align-items: center;
    transition: background-color 0.5s;
}

.project {
    overflow: hidden;
    display: flex;
    justify-content: center;
}

.project img {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    pointer-events: none;
}

.flex-projects {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;
    flex: 1 1 200px;
    justify-content: center;
}


.card {
    width: 70vw;
    max-width: 700px;
    padding: 0.5em;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: 16px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.24), 0 4px 8px 0 rgba(0,0,0,0.19);
}
.card h3 {
    transition: color 0.5s;
}

.title {
  font-size: min(1em, 35px);
}

.card img {
    display: block;
}

.card:hover h3 {
    color: #11bb99;
}

.description {
    margin: 1em;
}

.skills {
    margin: 1em;
}

.links {
    margin: 0.5em;
}

.links a{
    padding-right: 1em;
}
.container-about {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 16px;
    align-items: center;
    width: calc(100% - 20px); 
}

.profile-pic{
    display: block;
    width: 6em;
    max-width: 400px;
    border-radius: 50%;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    margin-bottom: 26px;
}

.summary {
    width: 100%;
    max-width: 700px;
}

.summary h4 {
    margin-top: 16px;
    
}

.summary p {
    padding-top: 16px;
    margin-right: 0;
    padding-right: 15px;
}

.flex-about {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    box-sizing: border-box;
    margin: 1em 0 1em 0;
}

.skill-areas {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.skill-area {
    width: 400px;
    max-width: 100%;
}

.skill-area h4 {
    text-decoration: underline;
}

.skills {
    list-style-type: none;
    margin: 16px;
}
.container-contact {
    border-top: 1px solid rgba(119, 119, 119, 0.185);
    height: 15vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact-methods { 
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-methods a {
    color: rgb(29, 30, 31);
    text-decoration: none;
    align-items: center;
    display: block;
    padding: min(2em, 16px);
    font-size: min(1em, 16px);
    margin: 0 32px 0 32px;
}


@media screen and (max-width: 600px) {
  .contact-methods a {
      content: none;
      color: transparent;
      font-size: 0;
      line-height: 0;
      padding: 2em;
  }
  .contact-methods a::after {
      margin: 1em;

  }
  .contact-methods {
      margin: 1em;
  }

}

.container-contact img {
    margin-right: .5em;
}

.container-contact p {
    font-size: 12px;
}
